* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  width: 100%;
  scroll-behavior: smooth;
  overflow-y: visible;
}
body {
  min-height: 100vh;
  position: static;
  overflow-y:scroll;
  overflow-x: hidden;
  background-color: #ffffff;
  max-width: 100vw;
}
.nav {
  display: flex;
  width: 100% !important;
  transition: 0.1ms;
  justify-content: space-around;
  align-items: center;
  background-color: #f8f8ff;
  height: 15vh;
  margin-bottom: 40px;
  box-shadow: 1px 2px 8px #eeb3b3;
}
.nav a {
  text-decoration:none;
  font-size: 28px;
  color: rgb(243, 78, 18);
}
.recipe-h1 {
  text-shadow: 1px 1px white, -1px -1px #444;
  filter: drop-shadow(10px 10px 1.5px gray);
  padding: 10px;
  font-weight: 700;
  letter-spacing: 2.5px;
}
h2 {
  text-align: center;
  position: absolute;
  text-shadow: 1px 1px white, -1px -1px #444;
  font-weight: 700;
  letter-spacing: 2.5px;
}
.mainDiv {
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(350px, 1fr));
  object-fit: cover;
  place-items: center;
  grid-gap:10px;
  grid-row: 25px;
  grid-template-rows: auto 1fr auto;
}
.input-search {
  position: absolute;
  top: 17%;
  left: 80%;
  border: 1px solid #E28F25;
}
.item {
  display: grid;
  place-items: center;
  box-shadow:inset 1px 1px 8px #E28F25;
  padding: 10px 0;
  width: 340px;
  border-radius: 4px;
  margin-top: 10px;
}
.item:hover {
  box-shadow: 1px 1px 8px #E28F25;
  transition: 300ms;
  transform: scale(1.005);
}
.item a {
  padding:0 5px;
}
.item img {
  height: 250px !important;
  width: 100%;
  display: flex;
  object-fit: cover;
  justify-content: center;
  align-items: center;
}
.item h3 {
  font-size: 18px;
  color: gray;
}
.item p {
  font-size: 20px;
  margin-top: -8px;
  font-weight: 700;
  margin-bottom: 8px;
}
.welcome-page {
  background-color: lightcoral;
  width: 100%;
  height: 100vh;
}

@media (max-width: 750px) {
  body {
    overflow-x: hidden;
  }
  .mainDiv {
    padding-top: 130px;
  }
  .home h1 {
    font-size: 25px;
    color: gray;
    letter-spacing: 1px;
  }
   .recipe-h1 {
    font-size: 18px;
  }
  .input-search {
    position: absolute;
    top: 12%;
    padding: 6px 15px;
    border: 1px solid #FF0000;
    left: 58%;
    width: 35%;
  }

  .input-search::placeholder {
    color: red;
  }
  .nav {
    width: 100%;
    height: 10vh;
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
  }
  .nav a {
    font-size: 12px;
    padding: 7px 10px;
  }
 
  .sidenav {
    width: 100%;
    z-index: -3;
    background-color: #F8F8FF;
    position: fixed;
    top: 19%;
    font-weight: 600;
    height: 25vh;
    outline: none;
    top: -110%;
    border: none;
    transition: all 0.4s;
    z-index: 5;
    border-bottom: 2px solid black;
    transition : border 400ms ease-out;
  }

.sidenav:hover {
   border-bottom : 180px solid #eca685;  
   color:black;
}
  .sidenav.active {
    top: 10%;
  }
  .sidenav.active ul   {
    display: flex;
    margin: 5px;
    align-items: center;
    justify-content: space-evenly;
    font-size: 16px;
    padding: 5px;
  }
  .sidenav-menu a {
    padding: 5px 13px;
    border-radius: 3px;
    color: black;
  }
  .sidenav-menu a:hover {
    background-color: orangered;
    color: #F8F8FF;
  }
  .sidenav .name {
    font-size: 20px;
    text-align: center;
    color: #101010;
    font-weight: 700;
  }
  .sidenav svg {
    margin-bottom: 30px;
    margin-left: 20%;
  }
  .giHam {
    cursor: pointer;
    font-size: 40px;
    margin-left: -20px;
  }
  .giHam:active {
    transform: rotate(320deg);
  }
}
@media (min-width: 750px) and (max-width: 900px) {
  .mainDiv {
    padding-top: 180px;
  }
    .recipe-h1 {
      font-size: 24px;
    }
    .input-search {
      position: absolute;
      top: 19%;
      padding: 6px 15px;
      left: 57%;
      width: 40%;
    }
    .input-search::placeholder {
      color: red;
    }
    .nav {
      width: 100%;
      height: 16vh;
      z-index: 10;
      position: fixed;
      justify-content:  start;
      top: 0;
      left: 0;
    }
    .nav ul {
      display: flex;
      justify-content: space-around;
    }   
    .nav ul a {
      font-size: 23px;
      margin-right: 18px;
      padding: 5px 10px;
      font-weight: 700;
      letter-spacing: 0.5px;
    }
    .nav ul a:hover {
      color: black;
      transform: rotate(-3deg);
    }
    .nav ul a svg:hover {
      fill: black;
    }
    .giHam {
      visibility: hidden;
    }
    .name {
      margin-left: 15px;
    }
}
@media (min-width: 901px) and (max-width: 1550px) {
  .mainDiv {
    padding-top: 180px;
  }
    .recipe-h1 {
      font-size: 30px;
    }
    .input-search {
      position: absolute;
      top: 19%;
      padding: 6px 15px;
      left: 63.2%;
      width: 35%;
    }
    .input-search::placeholder {
      color: red;
      padding-left: 20px;
    }
    .nav {
      width: 100%;
      height: 16vh;
      z-index: 10;
      position: fixed;
      display: flex;
      justify-content:  space-around;
      top: 0;
      left: 0;
    }
    .nav ul li {
      display: flex;
      justify-content: space-between;
    }   
    .nav ul a {
      font-size: 23px;
      margin-right: 25px;
      padding: 5px 10px;
      font-weight: 700;
      letter-spacing: 0.5px;
    }
    .nav ul a:hover {
      color: black;
      transform: rotate(-3deg);
    }
    .nav ul a svg:hover {
      fill: black;
    }
    .giHam {
      visibility: hidden;
    }
    .name {
      margin-left: 15px;
    }
    .navbar-svg {
      margin-top: 8px;
      margin-left: -30px;
    } 
}
.home {
  display: grid;
  place-items: center;
}

.home h1 {
  margin-top: 160px;
  margin-bottom: 30px;
  padding: 10px;
  text-align: center;
}
.home .home-img {
  width:350px;
  place-items: center;
  filter: saturate(13);
}